@import "../base/variables.less";

.not-found {
  display: flex;
  flex-direction: column;
  place-content: center;
  align-items: center;
  gap: 30px;
  height: 100%;
  padding: 0 30px;


  @media @tablet {
    padding: 0;
  }
}

.not-found__image {
  width: 100%;
  max-width: 300px;
}

.not-found__title {
  margin: 0;
  text-align: center;
}
